import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import { AppBackButton } from "../../components/AppComponents";
import { AppStore } from "../../lib/AppStore";
import Main from "../Main";
import { ApiGet, getData, CLAIM_STATUS } from "../../lib/AppHelper";
import moment from "moment";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPhoneSquare } from "@fortawesome/free-solid-svg-icons";
import { InputGroup } from "../../components/form/InputGroup";
import { InputSelect } from "../../components/form/InputSelect";
import Button from "../../components/Button";
import ReactHtmlTableToExcel from "react-html-table-to-excel";
import InputDate from "../../components/form/InputDate";
import { CSVLink } from "react-csv";

export class ResellerClaimReport extends Component {
  MessageRef = React.createRef();
  state = {
    reports: [],
    headers: [
      { label: "Created Date", key: "claim_register_date" },
      { label: "Product Name", key: "product_name" },
      { label: "Customer Name", key: "customer_name" },
      { label: "Customer Mobile", key: "mobile" },
      { label: "Claim Start Date", key: "start_date" },
      { label: "Claim End Date	", key: "end_date" },
      { label: "Claim Eligible Days	", key: "eligible_days" },
      { label: "Claim Amount", key: "claim_amount" },
      { label: "Claim Approved Amount", key: "approved_amount" },
      { label: "Claim Reason", key: "claim_reason" },
      { label: "Claim Rejected Reason", key: "claim_reject_reason" },
      { label: "Claim Remarks", key: "remarks" },
      { label: "Claim External Statue", key: "external_status" },
      { label: "Claim Internal Statue", key: "internal_status" },
      { label: "Reseller Name", key: "reseller_name" },
      { label: "Account Holder Name", key: "account_holder_name" },
      { label: "Accounr Number", key: "account_number" },
      { label: "IFSC Code", key: "ifsc_code" },
      { label: "Bank Name", key: "bank_name" },
      { label: "Hospital Name", key: "hospital_name" },
      { label: "Policy Code", key: "policy_code" },
    ],
    count: {
      sold: 0,
      sold_count: 0,
      unsold: 0,
      unsold_count: 0,
    },
    params: {
      from: moment().format("YYYY-MM-DD"),
      to: moment().format("YYYY-MM-DD"),

      status: "",
    },
    employees: [],
  };

  onChangeHandler = (e, key) => {
    const { params } = this.state;
    params[key] = e.target.value;
    this.setState({ params: params });
  };

  onChangeHandlerDate = (e, key) => {
    const { params } = this.state;
    params[key] = new Date(e).toISOString().split("T")[0];
    this.setState({ params: params });
  };

  render() {
    const { params, reports, count, employees, headers } = this.state;
    return (
      <Main MessageRef={this.MessageRef}>
        <div className="grid md:grid-cols-2 gap-2">
          <div>
            <AppBackButton to="/">Back</AppBackButton>
          </div>
          <div className="text-right">
            <div style={{ maxWidth: "20%" }} className="float-right">
              <button
                style={{ fontSize: "12px" }}
                className="bg-red-500 hover:bg-red-700 text-white font-bold py-1 px-2 rounded-full"
              >
                <CSVLink
                  data={reports}
                  headers={headers}
                  filename={"claimReports.csv"}
                >
                  Download
                </CSVLink>
              </button>
            </div>
          </div>
        </div>

        <div className="grid md:grid-cols-5 gap-4">
          <div>
            <label className="flex text-sm text-gray-700 justify-left pb-2">
              Select Team
            </label>
            <select
              onChange={(e) => this.onChangeHandler(e, "employee_id")}
              className="focus:ring-0 focus:border-primary focus:bg-primary-50 block w-full px-4 text-base border-gray-300 rounded-xl disabled:bg-gray-100"
            >
              <option value="">Me</option>
              {employees.map((option, index) => {
                return (
                  <option value={option.id}>
                    {option.name} ({option.mobile})
                  </option>
                );
              })}
            </select>
          </div>
          <div>
            <InputSelect
              requiredMark=""
              value={params.status}
              attrs={{
                value: params.status,
                onChange: (e) => this.onChangeHandler(e, "status"),
              }}
              name="status"
              label="Claim Type"
              options={CLAIM_STATUS}
            />
          </div>
          <div>
            <InputDate
              label="From"
              value={params.from}
              placeholder="from"
              attrs={{
                onChange: (e) => this.onChangeHandlerDate(e, "from"),
              }}
            />
          </div>
          <div>
            <InputDate
              label="To"
              value={params.to}
              placeholder="To"
              attrs={{
                onChange: (e) => this.onChangeHandlerDate(e, "to"),
              }}
            />
          </div>

          <div style={{ maxWidth: "50%", paddingTop: "22px" }}>
            <Button
              attrs={{ type: "button", onClick: this.handleClick }}
              size="md"
              title={"Search"}
            />
          </div>
        </div>

        <div className="flex flex-col">
          <div className="overflow-x-auto sm:-mx-6 lg:-mx-8">
            <div className="py-2 inline-block min-w-full sm:px-6 lg:px-8">
              <div className="overflow-x-auto">
                <table className="min-w-full border" id="table-to-xls">
                  <thead className="border-b">
                    <tr>
                      <th className="text-sm  font-large px-6 py-4 whitespace-nowrap text-left">
                        Created Date
                      </th>
                      <th className="text-sm  font-large px-6 py-4 whitespace-nowrap text-left">
                        Product
                      </th>
                      <th className="text-sm font-large px-6 py-4 whitespace-nowrap text-left">
                        Name
                      </th>
                      {/* <th className="text-sm font-large px-6 py-4 whitespace-nowrap text-left">
                        Mobile
                      </th> */}
                      <th className="text-sm  font-large px-6 py-4 whitespace-nowrap text-left">
                        Total Days
                      </th>
                      <th className="text-sm  font-large px-6 py-4 whitespace-nowrap text-left">
                        Claim Amount
                      </th>
                      <th className="text-sm  font-large px-6 py-4 whitespace-nowrap text-left">
                        Approved Amount
                      </th>
                      <th className="text-sm  font-large px-6 py-4 whitespace-nowrap text-left">
                        Status
                      </th>
                    </tr>
                  </thead>
                  {reports.map((record, index) => {
                    return (
                      <tbody key={index}>
                        <tr
                          className={`border-b ${
                            record.internal_status == "SETTELED" ||
                            record.actual_status == "SETTELED" ||
                            record.actual_status == "SETTELED"
                              ? "bg-green-100"
                              : ""
                          }
                          ${
                            record.internal_status == "REJECT"
                              ? "bg-red-100"
                              : ""
                          }
                          ${
                            record.external_status == "QUERY"
                              ? "bg-yellow-100"
                              : ""
                          }`}
                          key={index}
                        >
                          <td className="text-sm  font-bigger px-6 py-4 whitespace-nowrap">
                            {moment(record.claim_register_date).format(
                              "DD, MMM YYYY"
                            )}
                          </td>
                          <td className="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {record.product_name}
                          </td>
                          <td className="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {record.customer_name}
                          </td>
                          {/* <td className="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {record.mobile}
                          </td> */}
                          <td className="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {record.eligible_days}
                          </td>
                          <td className="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {record.claim_amount}
                          </td>
                          <td className="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {record.approved_amount}
                          </td>

                          <td className="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {record.external_status
                              ? record.external_status
                              : record.internal_status}
                          </td>
                        </tr>
                      </tbody>
                    );
                  })}
                </table>
              </div>
            </div>
          </div>
        </div>
      </Main>
    );
  }

  componentDidMount() {
    this.getReports();
    this.getTeam();
  }
  handleClick = () => {
    this.getReports(this.state.params);
  };

  getTeam = async () => {
    const response = await ApiGet(`team`);
    if (response.status === "success") {
      this.setState({ employees: response.data });
    }
  };

  getReports = async (request) => {
    AppStore.dispatch({ type: "LOADING", loading: true });
    const response = await ApiGet("report/reseller-claims", request);
    if (response.status === "success") {
      if (response.data.items) {
        const items = Object.values(response.data.items);
        this.setState({ reports: items });
        this.setState({ count: response.data });
      } else {
        this.state.reports = [];
      }
    } else {
      // this.MessageRef.current.show(response.message);
    }

    AppStore.dispatch({ type: "LOADING", loading: false });
  };
}

const mapStateToProps = (state) => {
  const { loading } = state;
  return {
    loading: loading,
  };
};
export default withRouter(ResellerClaimReport);
