import React from "react";
import { Steps } from "rsuite";

function Stepper({ activeStep = 0 }) {
  return (
    <>
      <Steps small current={activeStep}>
        <Steps.Item title="Select product" />
        <Steps.Item title="Select File" />
        <Steps.Item title="Prepration" />
        <Steps.Item title="Upload" />
      </Steps>
    </>
  );
}

export default Stepper;
