import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import { AppBackButton } from "../../components/AppComponents";
import { AppStore } from "../../lib/AppStore";
import Main from "../Main";
import { ApiGet, setMessage } from "../../lib/AppHelper";
import moment from "moment";
import Button from "../../components/Button";
import InputDate from "../../components/form/InputDate";
import { CSVLink } from "react-csv";
import { Pagination } from "rsuite";

export class LeadCaptureReport extends Component {
  constructor(props) {
    super(props);
    this.onFilterChange = this.onFilterChange.bind(this);
    this.getReports = this.getReports.bind(this);
  }
  MessageRef = React.createRef();

  state = {
    reports: [],
    headers: [
      { label: "Member Name", key: "name" },
      { label: "Member Email", key: "email" },
      { label: "Member Mobile", key: "mobile" },
      { label: "Product Name", key: "product_master.product_name" },
      { label: "Created At", key: "created_at" },
      { label: "Gender", key: "gender" },
      { label: "Pin Code", key: "pin_code" },
    ],
    total: 0,
    params: {
      from: moment().format("YYYY-MM-DD"),
      to: moment().format("YYYY-MM-DD"),
      page: 1,
      count: 10,
      status: "",
      all: false,
      employee_id: "",
    },
    employees: [],
  };

  onChangeHandler = (e, key) => {
    const { params } = this.state;
    params[key] = e.target.value;
    this.setState({ params: params });
  };

  onChangeHandlerDate = (e, key) => {
    const { params } = this.state;
    params["all"] = false;
    params[key] = new Date(e).toISOString().split("T")[0];
    this.setState({ params: params });
  };

  render() {
    const { params, reports, count, employees, headers } = this.state;
    return (
      <Main MessageRef={this.MessageRef}>
        <div className="grid md:grid-cols-2 gap-2">
          <div>
            <AppBackButton to="/">Back</AppBackButton>
          </div>
          <div className="text-right">
            <div className="float-right flex space-x-2">
              <button
                style={{ fontSize: "12px" }}
                className="bg-red-500 hover:bg-red-700 text-white font-bold py-1 px-2 rounded-full"
              >
                <CSVLink
                  data={reports}
                  headers={headers}
                  filename={"leadReports.csv"}
                >
                  Download
                </CSVLink>
              </button>
            </div>
          </div>
        </div>

        <div className="grid md:grid-cols-5 grid-cols-2 gap-x-2">
          <div>
            <label className="flex text-sm text-gray-700 justify-left pb-2">
              Select Team
            </label>
            <select
              onChange={(e) => this.onChangeHandler(e, "employee_id")}
              className="focus:ring-0 focus:border-primary focus:bg-primary-50 block w-full px-4 text-base border-gray-300 rounded-xl disabled:bg-gray-100"
            >
              <option value="">Me</option>
              {employees.map((option, index) => {
                return (
                  <>
                    <option value={option.id} key={index}>
                      {option.name} ({option.mobile})
                    </option>
                  </>
                );
              })}
            </select>
          </div>
          <div>
            <InputDate
              label="From"
              value={params.from}
              placeholder="from"
              attrs={{
                onChange: (e) => this.onChangeHandlerDate(e, "from"),
              }}
            />
          </div>
          <div>
            <InputDate
              label="To"
              value={params.to}
              placeholder="To"
              attrs={{
                onChange: (e) => this.onChangeHandlerDate(e, "to"),
              }}
            />
          </div>

          <div style={{ maxWidth: "50%", paddingTop: "27px" }}>
            <Button
              attrs={{ type: "button", onClick: this.handleClick }}
              size="md"
              title={"Search"}
            />
          </div>
        </div>

        <div className="flex flex-col">
          <div className="overflow-x-auto sm:-mx-6 lg:-mx-8">
            <div className="py-2 inline-block min-w-full sm:px-6 lg:px-8">
              <div className="overflow-x-auto">
                <table className="min-w-full border" id="table-to-xls">
                  <thead className="border-b">
                    <tr>
                      <th className="text-sm  font-large px-6 py-4 whitespace-nowrap text-left">
                        Product
                      </th>
                      <th className="text-sm  font-large px-6 py-4 whitespace-nowrap text-left">
                        Member Name
                      </th>
                      <th className="text-sm  font-large px-6 py-4 whitespace-nowrap text-left">
                        Phone no
                      </th>
                      <th className="text-sm  font-large px-6 py-4 whitespace-nowrap text-left">
                        Email
                      </th>
                      <th className="text-sm  font-large px-6 py-4 whitespace-nowrap text-left">
                        Created At
                      </th>
                      <th className="text-sm  font-large px-6 py-4 whitespace-nowrap text-left">
                        Gender
                      </th>
                      <th className="text-sm  font-large px-6 py-4 whitespace-nowrap text-left">
                        Pincode
                      </th>
                    </tr>
                  </thead>
                  {reports.map((record, index) => {
                    return (
                      <tbody key={index}>
                        <tr className="border-b" key={index}>
                          <td
                            style={{ maxWidth: "200px" }}
                            className="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap"
                          >
                            {record.product_master.product_name}
                          </td>
                          <td className="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {record.name}
                          </td>
                          <td className="text-sm capitalize text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {record.mobile}
                          </td>
                          <td className="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {record.email}
                          </td>
                          <td className="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {record.created_at}
                          </td>
                          <td className="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {record.gender}
                          </td>
                          <td className="text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap">
                            {record.pin_code}
                          </td>
                        </tr>
                      </tbody>
                    );
                  })}

                  <div style={{ padding: 20 }}>
                    <Pagination
                      prev
                      next
                      first
                      last
                      ellipsis
                      boundaryLinks
                      maxButtons={2}
                      size="xs"
                      layout={["total", "-", "pager", "skip"]}
                      total={this.state.total}
                      limit={10}
                      activePage={this.state.params.page}
                      onChangePage={this.onPageChange}
                    />
                  </div>
                </table>
              </div>
            </div>
          </div>
        </div>
      </Main>
    );
  }

  onPageChange = (page) => {
    if (page === false) return false;
    this.setState({ params: { page: page } }, () => {
      this.getReports(this.state.params);
    });
  };

  onFilterChange = (e) => {
    this.setState(
      { params: { ...this.state.params, all: e.target.value } },
      () => {
        this.getReports(this.state.params);
      }
    );

    // this.getReports(this.state.params);
  };

  componentDidMount() {
    this.getReports(this.state.params);
    this.getTeam();
  }
  handleClick = () => {
    const { params } = this.state;
    params["all"] = false;
    this.getReports(params);
  };

  getTeam = async () => {
    const response = await ApiGet(`team`);
    if (response.status === "success") {
      this.setState({ employees: response.data });
    }
  };

  getReports = async (request) => {
    AppStore.dispatch({ type: "LOADING", loading: true });
    await ApiGet("reseller/customer-leads", request)
      .then((response) => {
        if (response.data) {
          const items = Object.values(response.data.data);
          this.setState({ reports: items, total: response.data.total });
        } else {
          this.state.reports = [];
        }
        const me = ApiGet("me");
        if (me.status === "success") {
          AppStore.dispatch({ type: "USER_UPDATED", user: me.data });
        }
      })
      .catch((error) => {
        this.state.reports = [];
        setMessage("somthing went wrong", "error", () => {});
      });
    AppStore.dispatch({ type: "LOADING", loading: false });
  };
}

const mapStateToProps = (state) => {
  const { loading } = state;
  return {
    loading: loading,
  };
};
export default withRouter(LeadCaptureReport);
